.App {
  text-align: center;
}

.h1{

  font: 'Montserrat';

}

.content{
  align-self: center;
}

.showButton{
  color: rgb(0, 0, 0);
  background-color: rgb(40, 208, 158);
  padding: 5px 15px;
  border-radius: 5px;
  outline: 0;
  text-transform: uppercase;
  margin: 20px;
  cursor: pointer;
  box-shadow: 0px 2px 2px lightgray;
  transition: ease background-color 250ms;
}